'use client'
import styles from "../../page.module.css";
import Form from 'react-bootstrap/Form';
import EntryBox from "../../_components/entrybox";
import Submit from "../../_components/submit";
import Select from "../../_components/select";
import { useEffect, useReducer } from "react";
import CheckBox from "../../_components/checkbox";
import { fetch } from "../../_scripts/communications";
import backgroundImage from "../../../public/Green.png"
import FormTitle from "@/app/_components/formtitle";
import Feedback from "@/app/_components/feedback";

let submitObject = {};

const defaultPeriod = 90;

//I HATE JS FOR MAKING ME DO THIS STUPID FORMATTING FOR DATES
//Thank you chat GPT for doing it for me

function calculateFutureDate(inputDate = null,numDays=defaultPeriod ) {
    // If inputDate is not provided, default to today's date
    if (inputDate === null) {
        var xtoday = new Date();
    } else {
        var dateParts = inputDate.split("-");
        var xtoday = new Date(dateParts[0], dateParts[1] - 1, dateParts[2]); // month is 0-based
    }

    // Add numDays to the provided date
    var futureDate = new Date();
    futureDate.setDate(xtoday.getDate() + parseInt(numDays));

    // Format the future date into yyyy-mm-dd format
    var yyyy = futureDate.getFullYear();
    var mm = String(futureDate.getMonth() + 1).padStart(2, '0'); // Adding 1 because getMonth() is zero-based
    var dd = String(futureDate.getDate()).padStart(2, '0');

    var dateString = yyyy + '-' + mm + '-' + dd;
    return dateString;
}

const initState = {
    'LCS Account Number': {
        value: ''
    },
    'Company Code':{
        value:''
    },
    'Implementation End':{
        value: calculateFutureDate()
    },
    'Implementation Period':{
        value:defaultPeriod
    },
    'In Implementation':{
        isChecked:true
    },
    'Submit':{
        Color:'primary',
        Text:'Submit'
    },
    'Feedback':{
        color:'success',
        code:'',
        title:'',
        text:'',
        hidden: true
    }
};


const reducer = (state, action)=>{

    let newState = {...state};

    switch (action.type){

        case 'update-Value':{

            newState[action.label].value = action.value;

            /*
            if(action.label == 'Implementation Period'){

                newState['Implementation End'].value = calculateFutureDate(state['Implementation End'].value, action.value)

            }
            */

            newState.Submit.Color = 'primary';
            newState.Submit.Text = 'Submit'

            console.log(newState['Implementation End'].value);

            return newState

            break
        }

        case 'submit-Clicked':{

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Error':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Success':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;

        }
        
        case 'mark-Checked':{

            newState[action.label].isChecked = action.value;

            return newState

            break;

        }
        case 'load-Feedback':{

            newState['Feedback'].hidden = false;
            newState['Feedback'].color = action.color;
            newState['Feedback'].code = action.code;
            newState['Feedback'].title = action.title;
            newState['Feedback'].text = action.text;

            return newState;

            break;

        }
        

    }
};

export default function CreateStudent(){


    const [state, dispatch] = useReducer(reducer,initState);

    let submitObject = {
        LSManager_Account:state['LCS Account Number'].value,
        Company_Code: state['Company Code'].value,
        Implementation_End:state['Implementation End'].value,
        isImplementation: state['In Implementation'].isChecked
    }


    return (

        <main className={styles.main} style={{backgroundImage: `url(${backgroundImage.src})`}}>
            
          <Form>
            <FormTitle text='Create Account'></FormTitle>
            <EntryBox
                label='LCS Account Number'
                type = 'numeric'
                reminder ='Please Enter for LSManager'
                placeholder = ''
                value = {state['LCS Account Number'].value}
                dispatch={dispatch}
            ></EntryBox>
            <EntryBox
                label='Company Code'
                type = 'text'
                reminder =''
                placeholder = ''
                value = {state['Company Code'].value}
                dispatch={dispatch}
            ></EntryBox>
            <CheckBox
                label="In Implementation"
                reminder='Check if Implementation'
                isChecked={state['In Implementation'].isChecked}
                dispatch={dispatch}
            ></CheckBox>
            <EntryBox
                label='Implementation End'
                type = 'date'
                reminder =''
                placeholder = ''
                hidden = {!state['In Implementation'].isChecked}
                value = {state['Implementation End'].value}
                dispatch={dispatch}
            ></EntryBox>
            <Submit 
                method="GET"
                endpoint="CreateAccount"
                dispatch={dispatch}
                data={submitObject}
                color={state.Submit.Color}
                text={state.Submit.Text}
            ></Submit>
            <Feedback
                color={state['Feedback'].color}
                code={state['Feedback'].code}
                title={state['Feedback'].title}
                text={state['Feedback'].text}
                hidden={state['Feedback'].hidden}
            ></Feedback>
          </Form>
        </main>
    );
}